import html from './img-slider.html';

const template = document.createElement('template');

template.innerHTML = `
    <style>
        /* component reset */
        a {
            text-decoration: none;
        }

        * {
            box-sizing: border-box;
            font-family: 'Kumbh Sans';
            margin: 0;
        }
        /* component reset */

        .slider {
            position: relative;
            width: 320px;
            height: 100%;
            margin: auto;

            @media(min-width: 655px) {
                width: 655px;
            }

            @media(min-width: 900px) {
                margin-top: 80px;
            }

            @media(min-width: 1024px) {
                width: 400px;
            }
        }

        .slides {
            display: grid;
            grid-auto-flow: column;
            grid-auto-columns: 320px;

            gap: 15px;

            overflow-x: auto;
            scroll-snap-type: inline mandatory;
            scrollbar-width: none;

            @media(min-width: 1024px) {
                grid-auto-columns: 400px;
            }
        }
   
        ::slotted([slot="slide"]) {
            width: 100%;
            height: 300px;
            object-fit: cover;

            scroll-snap-align: start;
        }

        @media(min-width: 350px) {
            ::slotted([slot="slide"]) {
                border-radius: 15px;
            }
        }

        @media(min-width: 1024px) {
            ::slotted([slot="slide"]) {
                height: 400px;
            }
        }

        .previous:hover, .next:hover {
            cursor: pointer;

        }

        .previous, .next {
            display: flex;
            justify-content: center;
            align-items: center;

            position: absolute;
            top: 50%;
            margin: 0 15px;

            background-color: hsl(0, 0%, 100%);
            width: 30px;
            height: 30px;
            border-radius: 30px;

            @media(min-width: 1024px) {
                display: none;
            }
        }

        .previous > img, .next > img {
            height: 15px;
            width: 10px;
        }

        .next {
            right: 0;
        }


        .thumbnails {
            display: grid;
            grid-auto-flow: column;
            justify-content: space-between;
            margin-top: 30px;
            overflow-x: auto;
            scroll-snap-type: inline mandatory;
            scrollbar-width: none;
        }

        ::slotted([slot="thumbnail"]) {
            display: none;
        }

       @media(min-width: 1024px) {
            ::slotted([slot="thumbnail"]) {
                display: block;

                // width: 80px;
                width: 160px;
                height: 80px;
                border-radius: 10px;
            }
       }

       .previous-thumbnail {
            background-color: red;
       }


    </style>
    ${html}
`;

class ImgSlider extends HTMLElement {
    constructor() {
        super();
        this.attachShadow({ mode: 'open' });
        this.shadowRoot.appendChild(template.content.cloneNode(true));

        this.slidesContainer = this.shadowRoot.querySelector('.slides');
        this.slideSlot = this.shadowRoot.querySelector('slot[name="slide"]');
        this.images = this.slideSlot.assignedElements();
        this.previousBtn = this.shadowRoot.querySelector('.previous');
        this.nextBtn = this.shadowRoot.querySelector('.next');


        this.previousBtn.addEventListener('click', (e) => this.navigateImages(e));
        this.nextBtn.addEventListener('click', (e) => this.navigateImages(e));

    }

    navigateImages(e) {
        const clickedBtn = e.target.closest('custom-button').classList[0];
        const scrollPosition = this.slidesContainer.scrollLeft;

        if (clickedBtn === 'next') {
            this.slidesContainer.scrollTo({ left: scrollPosition + 320, behavior: 'smooth' });
        }

        if (clickedBtn === 'previous') {
            this.slidesContainer.scrollTo({ left: scrollPosition - 320, behavior: 'smooth' });
        }
    }

    thumbnailNavigation() {
        const activeImage = this.querySelector()

    }
}

customElements.define('img-slider', ImgSlider);

export { ImgSlider }
